<template>
  <div>
    <h1 style="margin-bottom: 30px;">我的简历</h1>
  <el-form ref="form" :model="form" label-width="150px" v-if="form" :rules="rules">
      <el-form-item :label="item.name+'：'" v-for="item,index in fromArr" :key="index" :prop="item.key">
         <el-date-picker v-if="item.type==='date'"
        v-model="form[item.key]"
      type="date" style="    width: 100%;"
      placeholder="选择日期">
    </el-date-picker>
          <el-input  v-if="item.type==='number'" maxlength="30" v-model="form[item.key]" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
           <el-select v-model="form[item.key]" placeholder="请选择" v-if="item.type==='select'" class="w100">
            <el-option
              v-for="item in item.Arr"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
           <el-select v-model="form[item.key]" placeholder="请选择" v-if="item.type==='selecteun'" class="w100">
            <el-option
              v-for="item in item.eun"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
          <el-input maxlength="30"  v-if="item.type===undefined" v-model="form[item.key]" ></el-input>
  </el-form-item>
  </el-form>
  <div style="    text-align: center;">
  <el-button class="btn_l" size="small" @click="BtnBack">取消</el-button>
  <el-button type="primary" size="small" class="btn_r" @click="BtnClick">提交</el-button>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rules: {
        appellation: [{ required: true, message: '请输入称谓', trigger: 'change' }],
        age: [{ required: true, message: '请输入年龄', trigger: 'change' }],
        sex: [{ required: true, message: '请输入性别', trigger: 'change' }],
        height: [{ required: true, message: '请输入身高', trigger: 'change' }],
        nation: [{ required: true, message: '请输入民族', trigger: 'change' }],
        nativePlace: [{ required: true, message: '请输入籍贯', trigger: 'change' }],
        boardingPlace: [{ required: true, message: '请输入学历', trigger: 'change' }],
        graduationSchool: [{ required: true, message: '请输入毕业学校', trigger: 'change' }],
        graduationTime: [{ required: true, message: '请选择毕业时间', trigger: 'change' }],
        certificateLevel: [{ required: true, message: '请输入证书等级', trigger: 'change' }],
        education: [{ required: true, message: '请输入现居住地', trigger: 'change' }],
        currentResidence: [{ required: true, message: '请输入上船地点', trigger: 'change' }],
        experienceSailing: [{ required: true, message: '请输入航海经验', trigger: 'change' }],
        jobApplication: [{ required: true, message: '请输入应聘职位', trigger: 'change' }],
        major: [{ required: true, message: '请输入专业', trigger: 'change' }],
        maritalStatus: [{ required: true, message: '请输入婚姻状况', trigger: 'change' }],
        monthlySalary: [{ required: true, message: '请输入要求月薪', trigger: 'change' }],
        originalPosition: [{ required: true, message: '请输入原任职务', trigger: 'change' }],
        politicalOutlook: [{ required: true, message: '请输入政治面貌', trigger: 'change' }],
        requiredGross: [{ required: true, message: '请输入要求总吨', trigger: 'change' }],
        routeRequirements: [{ required: true, message: '请输入航线要求', trigger: 'change' }],
        shipRequirements: [{ required: true, message: '请输入船舶要求', trigger: 'change' }],
        technicalSkill: [{ required: true, message: '请输入特殊技能', trigger: 'change' }],
        userStatus: [{ required: true, message: '请选择特殊技能', trigger: 'change' }]

      },
      fromArr: [
        { key: 'appellation', name: '称谓' },
        { key: 'age', name: '年龄', type: 'number' },
        { key: 'sex', name: '性别', type: 'selecteun', eun: ['男', '女'] },
        { key: 'height', name: '身高', type: 'number' },
        { key: 'nation', name: '民族' },
        { key: 'nativePlace', name: '籍贯' },
        { key: 'boardingPlace', name: '学历', type: 'selecteun', eun: ['小学', '初中', '高中', '大学', '研究生', '博士'] },
        { key: 'graduationSchool', name: '毕业学校' },
        { key: 'graduationTime', name: '毕业时间', type: 'date' },
        { key: 'certificateLevel', name: '证书等级' },
        { key: 'education', name: '现居住地' },
        { key: 'currentResidence', name: '上船地点' },
        { key: 'experienceSailing', name: '航海经验' },
        {
          key: 'jobApplication',
          name: '应聘职位',
          type: 'selecteun',
          eun: [
            '船长',
            '大副',
            '二副',
            '三副',
            '水手长',
            '一水',
            '二水',
            '甲板实习生',
            '大厨',
            '服务生',
            '轮机长',
            '大管轮',
            '二管轮',
            '三管轮',
            '电机员',
            '机工长',
            '机工',
            '二机',
            '机舱实习生'
          ]
        },
        { key: 'major', name: '专业' },
        { key: 'maritalStatus', name: '婚姻状况', type: 'selecteun', eun: ['已婚', '未婚'] },
        { key: 'monthlySalary', name: '要求月薪', type: 'number' },
        { key: 'originalPosition', name: '原任职务' },
        { key: 'politicalOutlook', name: '政治面貌' },
        { key: 'requiredGross', name: '要求总吨', type: 'number' },
        { key: 'routeRequirements', name: '航线要求' },
        { key: 'shipRequirements', name: '船舶要求' },
        { key: 'technicalSkill', name: '特殊技能' },
        {
          key: 'userStatus',
          name: '在职状态',
          type: 'select',
          Arr: [
            { value: '0', label: '在职不考虑机会' },
            { value: '1', label: '在职考虑机会' },
            { value: '2', label: '离职状态' }

          ]
        }
      ],
      form: {

      },
      url: '/seaman-recruit/resume/add'

    }
  },
  methods: {
    getData () {
      this.$axios.get('/seaman-recruit/resume/queryByUserId', '').then(res => {
        if (res.data.code === 200) {
          if (res.data.data && res.data.data.appellation) {
            this.form = res.data.data
            this.url = '/seaman-recruit/resume/updateById' // 判断修改还是新增
          }
        } else {
          console.log(res.data.data)
          this.$message.error(res.data.data)
        }
      })
    },
    BtnBack () {
      this.$router.back()
    },
    BtnClick () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const datas = JSON.parse(JSON.stringify(this.form))
          datas.userName = JSON.parse(localStorage.getItem('user')).userName
          datas.userId = JSON.parse(localStorage.getItem('user')).id
          datas.graduationTime = this.tiemFun(datas.graduationTime)
          this.$axios.post(this.url, datas).then(res => {
            if (res.data.code === 200) {
              this.$message({
                message: '操作成功！',
                type: 'success'
              })
              this.$router.push('/home/user/JobList')
            } else {
              console.log(res.data.data)
              this.$message.error(res.data.data)
            }
          })
        } else {
          this.$message.error('请完善表单信息！')
        }
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
