<template>
  <div>
    <h1 style="margin-bottom: 20px;">{{$route.query.id?'编辑岗位':'新增岗位'}}</h1>
  <el-form ref="form" :model="form" :rules="rules" label-width="120px" style="width: 100%;">
      <el-form-item label="职位名称：" prop="positionName">
        <el-select v-model="form.positionName" class="w100" placeholder="请选择">
    <el-option
      v-for="item in AddArr"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
  </el-form-item>
      <el-form-item label="学历要求：" prop="education">
         <el-select v-model="form.education" class="w100" placeholder="请选择">
    <el-option
      v-for="item in EducationEnum"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
  </el-form-item>
      <el-form-item label="月薪：" prop="monthlySalary">
    <el-input maxlength="30" v-model="form.monthlySalary" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
      <el-form-item label="船龄(年)：" prop="shipAge">
    <el-input maxlength="30" v-model="form.shipAge"></el-input>
  </el-form-item>
      <el-form-item label="船舶类型：" prop="shipType">
   <el-select v-model="form.shipType" class="w100" placeholder="请选择">
    <el-option
      v-for="item in getShipTypeList"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
  </el-form-item>
      <el-form-item label="具体航线：" prop="specificRoute">
    <el-input maxlength="60" v-model="form.specificRoute"></el-input>
  </el-form-item>
      <el-form-item label="合同时长(年)：" prop="contractDuration">
    <el-input maxlength="10" v-model="form.contractDuration"></el-input>
  </el-form-item>
      <el-form-item label="上船地点：" prop="boardingPlace">
    <el-input maxlength="30" v-model="form.boardingPlace"></el-input>
  </el-form-item>
      <el-form-item label="招聘人数：" prop="positionNum">
    <el-input maxlength="30" v-model="form.positionNum" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
      <el-form-item label="总吨/马力：" prop="grossTon">
    <el-input maxlength="30" v-model="form.grossTon" oninput ="value=value.replace(/[^0-9.]/g,'')"></el-input>
  </el-form-item>
  </el-form>
  <div style="    text-align: center;">
  <el-button class="btn_l" size="small" @click="BtnBack">取消</el-button>
  <el-button type="primary" size="small" class="btn_r" @click="BtnClick">提交</el-button>
  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      form: {

      },
      rules: {
        positionName: [
          { required: true, message: '请选择职位名称', trigger: 'change' }
        ],
        education: [{ required: true, message: '请输入学历要求', trigger: 'change' }],
        monthlySalary: [{ required: true, message: '请输入月薪', trigger: 'change' }],
        shipAge: [{ required: true, message: '请输入船龄(年)', trigger: 'change' }],
        shipType: [{ required: true, message: '请选择船舶类型', trigger: 'change' }],
        specificRoute: [{ required: true, message: '请输入具体航线', trigger: 'change' }],
        contractDuration: [{ required: true, message: '请输入合同时长(年)', trigger: 'change' }],
        boardingPlace: [{ required: true, message: '请输入上船地点', trigger: 'change' }],
        positionNum: [{ required: true, message: '请输入招聘人数', trigger: 'change' }],
        grossTon: [{ required: true, message: '请输入总吨/马力', trigger: 'change' }]
      },
      Url: '/seaman-recruit/position/add'
    }
  },
  created () {
    if (this.$route.query.id) {
      this.getData()
      this.Url = '/seaman-recruit/position/updateById'
    }
  },
  methods: {
    BtnBack () {
      this.$router.back()
    },
    getData () {
      this.$axios.get('/seaman-recruit/position/queryById', '?id=' + this.$route.query.id).then(res => {
        if (res.data.code === 200) {
          this.form = res.data.data
        } else {
          this.$message.error(res.data.data)
        }
      })
    },
    BtnClick () {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const datas = JSON.parse(JSON.stringify(this.form))
          datas.userName = JSON.parse(localStorage.getItem('user')).userName
          datas.userId = JSON.parse(localStorage.getItem('user')).id
          this.$axios.post(this.Url, datas).then(res => {
            if (res.data.code === 200) {
              this.$message({
                message: '操作成功！',
                type: 'success'
              })
              this.$router.go(-1)
            } else {
              this.$message.error(res.data.data)
            }
          })
        } else {
          this.$message.error('请完善表单信息！')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.btn_l{
  margin-right: 40px;
}
.btn_r{
  margin-left: 40px;
}
.el-form-item{
  width: 50%;
  display:inline-block;
}
</style>
